.popup__message-text {
    font-family: Inter, Arial, sans-serif;
    font-size: 24px;
    font-weight: 900;
    line-height: 29px;
    text-align: center;
    color: #000;
  
    padding: 32px 36px 60px 36px;
    margin: 0;
    transition: .5s ease-in-out;
  }
  
  @media screen and (max-width: 550px) {
    .popup__message-text {
      font-size: 20px;
      line-height: 24px;
      padding: 40px 18px 50px 18px;
    }
  }